.item {
  background: #fff;
  border: 1px solid #e8e8e8;
  margin-bottom: 1.3rem;
  padding: 1.3rem;

}

  //BEM - block element modifier
// double underscore is just a convention
  .item__message {
    font-size: 1.3rem;
    font-style: italic;;
    font-weight: 300;
    text-align: center;
    color: purple;
  }

.item__message--empty {
  color: #999;
}

.item--position-1 {
  background: $green;
  border-color: $green-alt;
}

.item--position-2 {
  background: $blue;
  border-color: $blue-alt;
}

.item--position-3 {
  background: $red;
  border-color: $red-alt;
}
