.player {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.player__name {
  font-weight: 300;
  font-size: 1.8rem;
}

.player__stats {
  font-size: 1.3rem;
  font-style: italic;
  font-weight: 300;
}

.player__actions {
  flex-shrink: 0; // never shrink this no matter how big other things becomes

}
